import { SimpleChange } from '@angular/core';
import * as _ from 'lodash';

export function stringContains(value: string, search: string): boolean {
  return value.toLowerCase().indexOf(search.toLowerCase()) !== -1;
}

export function generateErrorMessage(message: string, httpError: any): string {
  return httpError && httpError.error && httpError.error.message
    ? `${message} (${httpError.error.message})`
    : message;
}

export function onlyUnique(value: any, index: number, self: any[]): boolean {
  return (
    index ===
    self.findIndex(
      selfValue => JSON.stringify(selfValue) === JSON.stringify(value)
    )
  );
}

export const nameOf = <T>(name: Extract<keyof T, string>): string => name;

export function flatMap<TDestination>(
  items: any[],
  key: string
): TDestination[] {
  return ([] as any[]).concat(...items.map(item => item[key]));
}

export function getZoneOffset(): string {
  const timezoneOffset = new Date().getTimezoneOffset();
  const isNegative = timezoneOffset < 0;
  let zoneOffset = (timezoneOffset * (isNegative ? -1 : 1)).toString();

  for (let i = zoneOffset.toString().length; i < 4; i++) {
    zoneOffset = '0' + zoneOffset;
  }

  return isNegative ? '-' : '+' + zoneOffset;
}

export function getEnumKeys(enumType: any): string[] {
  return Object.keys(enumType).filter(
    type => isNaN(<any>type) && type !== 'values'
  );
}

export function isPropertyChanged(simpleChange: SimpleChange): boolean {
  return (
    simpleChange && simpleChange.previousValue !== simpleChange.currentValue
  );
}

export function omitDeep(collection: any, ...excludeKeys: string[]): any {
  function omitFn(value: any): void {
    if (value && typeof value === 'object') {
      excludeKeys.forEach(key => {
        delete value[key];
      });
    }
  }

  const clonedCollection = _.cloneDeep(collection);

  return _.cloneDeepWith(clonedCollection, omitFn);
}

export function getProgress(loaded: number, total: number): number {
  return Math.round((loaded / total) * 100);
}
